:root {
  --primary: #232F3E;
  --secondary: #F3A847;
  --tertiary: #232f3e;
  --light: #F3A847;
  --dark: #272f32;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Karla', sans-serif;
  font-weight: 400;
  line-height: 1.5;
  font-size: 16px;
  color: var(--dark);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Rubik', 'Segoe UI', sans-serif;
  font-weight: 500;
  margin: 10px 0;
  line-height: 2.00;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  text-decoration: underline;
}

header {
  background: var(--primary);
  width: 100%;
}

header a {
  color: var(--light);
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: var(--secondary);

}

button:hover {
  opacity: .8;
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.error-text {
  color: var(--error);
}

@media screen and (max-width: 768px) {
  .card {
    width: 100%;
  }

  .card-header {
    width: 100%;
  }
}

.card-header {
  margin: auto;
  border-radius: 40px;
  background-color: var(--primary);
  font-weight: bold;
  text-align: center;
  width: 100%;
  
}

.home-text {
 color:#F3A847;
 font-weight: bold;
 padding: 10px;
  
}

.hero-text {
  color: #F3A847;
  font-weight: bolder;
  font-family: 'Rubik', 'Segoe UI', sans-serif;
  background-color: var(--primary);
  border-radius: 40px;

}

.category-names {
  font-weight: bolder;
  font-size: larger;
  color: #dd9638;
}

.body-main {
  background-image: url("../src/images/default-image-background.jpeg");
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.nav-text {
  text-align: right;
  float: right
}

.footer-main {
  margin-top: auto;
}

.footer-style {
  color: #F3A847;

}

.home-banner-font {
  font-weight: bolder;
  font-size: 45px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  display: flex;
  align-items: center;
}

.signup-login {
  font-size: 25px;
  float: right;
  vertical-align: middle;
  
}

.nav-links {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;

}

.login-card {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bolder;
}

@media screen and (max-width: 1015px) {
  .hero-text {
    font-size: 16px;
  }
  .card-header .row .card {
    width: 50%;
    align-items: center;
    justify-content: center;
  }
}

h3.hero-text.card-body {
  margin: auto;
  display: flex;
  text-align: center;
}

div.nav-links {
  vertical-align: text-bottom;
}

.hero-pad {
  padding: 10px;
}